#chat-button {
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,.2),
              0 6px 20px 0 rgba(0,0,0,.10);
  display: flex;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}

.cookie-settings-btn {
  position: fixed;
  line-height: 22px;
  z-index: 100;
  bottom: 0;
  left: 0;
  display: flex;
  background: $grey-cookie;
  font-weight: 600;
  border: solid $grey-cookie;
  border-width: 1px 1px 0 0;
  border-radius: 0;
  border-top-right-radius: 10px;
  padding: 0 10px;
  cursor: pointer;
  font-size: .8rem;
  color: $white;
  
  &:hover {
    background-color: $grey-cookie-light;
    border-color: $grey-cookie-light;
    box-shadow: 0 3px 35px rgba(0,0,0,.1);
  }
}

