 .box {
  background-color: maroon;//$grey-label;
  padding: 20px;
  font-size: .875rem;
  max-width: 280px;
  margin: 0 auto 50px;
 }
 
  .item {
    
    max-height: 70px;
    height: 70px;
    width: auto;
    align-items: center;
    padding: 0 20px;

  }

  .arrow {
    position: absolute;
    bottom: 10px;
    left: 50%;
    margin-left: -36px;

    &::after {
      content: "";
      margin: auto;
      display: inline-block;
      border-color: $grey-label transparent transparent;
      border-style: solid;
      border-width: 35px 36px 0;
    }
  }

  // Hamburger button

  .hamburger-box {
    display: inline-block;
    width: 33px;
    height: 19px;
    position: relative;
  }
  
  
  .hamburger-inner {
    left: 0;
    top: 10px;
  
    @include hamburger();
  
    &::before {
      @include hamburger();
      top: -10px;
      bottom: 0;
    }
  
    &::after {
      @include hamburger();
      top: 10px;
    }
  }
  
  .hamburger-spin:hover {
    .hamburger-inner {
  
      @include hamburger();
  
      transform: rotate(225deg);
      transition-delay: .12s;
      transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
      transition: .5s ease;
  
      &::before {
        @include hamburger();
        top:0;
        opacity: 0;
  
        transition: top .1s ease-out
          ,opacity .1s ease-out .12s;
  
        transform: rotate(45deg);
  
        &:hover {
          top: 10px;
        }
      }
  
      &::after {
        @include hamburger();
        bottom: 0;
        top: 0;
        transform: rotate(-90deg);
        transition: bottom .1s
          ease-out, transform .22s
          cubic-bezier(0.215, 0.610, 0.355, 1) .12s;
      }
    }
  }


